import "./App.scss";
import {
  Container,
  TextField,
  ButtonGroup,
  Drawer,
  AccordionDetails,
  Accordion,
  AccordionSummary,
  Stack,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import Swal from "sweetalert2";
import Btn from "./component/Button";
import Show from "./component/Show";
import { v4 as uuidv4 } from "uuid";

function App({
  text,
  setText,
  fontSize,
  setFontSize,
  alignItems,
  setAlignItems,
}) {
  const navigate = useNavigate();
  let historyTmp = JSON.parse(localStorage.getItem("history"));
  const [history, setHistory] = useState(historyTmp || []);

  useEffect(() => {
    localStorage.setItem("history", JSON.stringify(history));
  }, [history]);

  const printThis = () => navigate("/print");

  const [drawerShow, setDrawerShow] = useState(false);
  const toggleDrawer = () => setDrawerShow((pre) => (pre = !pre));

  const googleTranslate = () => {
    window.open(
      `https://translate.google.com/?hl=zh-CN&sl=zh-CN&tl=vi&text=${text}&op=translate`
    );
    //%0A =>\n
  };

  const save = async () => {
    const { value: title } = await Swal.fire({
      title: "",
      input: "text",
      inputPlaceholder: "",
      showCancelButton: true,
    });

    if (title) {
      setHistory((pre) => [
        {
          id: uuidv4(),
          title,
          text,
          fontSize,
          alignItems,
          date: today,
        },
        ...pre,
      ]);
    }
  };

  let today = new Date().toISOString().split("T")[0].replaceAll("-", "/");

  return (
    <Container className="home-container">
      <Stack mt={2} alignItems="flex-end">
        <ButtonGroup>
          <Btn value="" color="success" doClick={save} />
          <Btn value="" doClick={toggleDrawer} />
        </ButtonGroup>
      </Stack>
      <Stack spacing={2}>
        <TextField
          label=""
          variant="outlined"
          fullWidth
          multiline
          value={text}
          onChange={(e) => setText(e.target.value)}
        />
        <ButtonGroup>
          <Btn
            value=""
            doClick={() => googleTranslate()}
            color="success"
          />
          {/* <img src="/copy.png"/> */}
          <Btn
            value=""
            doClick={() => {
              setTimeout(async () => {
                let tmp = await window.navigator.clipboard.readText();
                setText(tmp);
              }, 0);
            }}
          />
        </ButtonGroup>
        <Drawer open={drawerShow} onClose={toggleDrawer}>
          <Container>
            <Stack direction="column">
              <Btn value="" doClick={toggleDrawer} color="error" />
              <h1></h1>
              {history.map((n) => (
                <ButtonGroup key={n.id}>
                  <Btn
                    value={n.title + " ->" + n.date}
                    doClick={() => {
                      setText(n.text);
                      setFontSize(n.fontSize);
                      setAlignItems(n.alignItems);
                    }}
                  />
                  <Btn
                    value=""
                    doClick={() =>
                      setHistory((pre) => pre.filter((m) => m.id !== n.id))
                    }
                    color="error"
                  />
                </ButtonGroup>
              ))}
            </Stack>
          </Container>
        </Drawer>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <h3>word ()</h3>
          </AccordionSummary>
          <AccordionDetails>
            <Stack spacing={4}>
              <ButtonGroup>
                <Btn value="" doClick={() => printThis()} color="success" />
                <Btn
                  value=""
                  doClick={() => {
                    Swal.fire({
                      icon: "question",
                      title: "?",
                      showCancelButton: true,
                    }).then((result) => {
                      if (result.isConfirmed) setText("");
                    });
                  }}
                  color="error"
                />
              </ButtonGroup>
              <h1></h1>
              <Show text={text} fontSize={fontSize} alignItems={alignItems} />

              <ButtonGroup>
                
                <Btn
                  value=""
                  doClick={() => setFontSize((pre) => Number(pre) + 5)}
                />
                <Btn
                  value=""
                  doClick={() => setFontSize((pre) => Number(pre) - 5)}
                />
                <Btn
                  value=""
                  doClick={() => setAlignItems("flex-start")}
                />
                <Btn value="" doClick={() => setAlignItems("center")} />
              </ButtonGroup>

              <ButtonGroup>
                
                <Btn
                  value="0915277990"
                  doClick={() => setText((pre) => pre + "\n 0915277990")}
                />
                <Btn
                  value=""
                  doClick={() => setText((pre) => pre + "")}
                />
                <Btn
                  value=""
                  doClick={() => setText((pre) => pre + "")}
                />
                <Btn
                  value=""
                  doClick={async () => {
                    const { value: times } = await Swal.fire({
                      title: "?",
                      input: "text",
                      inputValue: "5",
                      showCancelButton: true,
                    });

                    if (Number(times)) {
                      //
                      setText((pre) => (pre + "\n").repeat(times));
                    }
                  }}
                />
                <Btn
                  value=""
                  doClick={() => {
                    setText((pre) => `${pre} ${today}`);
                  }}
                />
              </ButtonGroup>
            </Stack>
          </AccordionDetails>
        </Accordion>
      </Stack>
    </Container>
  );
}

export default App;
